.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 100;
    transition: transform cubic-bezier(0.075, 0.82, 0.165, 1) 0.5s;
    transform: translateY(-130vh);
}

.active {
    transform: translateY(0);
}

.penSvg {
    position: absolute;
    right: 40px;
    bottom: 122px;
}

.userInfoModal {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    overflow: auto;
    position: absolute;
    z-index: 30;
    background: #fafafa;
    box-shadow: 0px 4px 48px rgba(0, 0, 0, 0.1);
    border-radius: 24px;
}

.saveBtn {
    padding: 0;
    border: none;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    
    height: 50px;
    
    background: #8baa36;
    border-radius: 6px;
    
    font-family: 'Poppins';
    font-size: 14px;
    line-height: 18px;
    
    color: #fafafa;
}

.nameInput {
    height: 50px;
    padding: 0;
    padding-left: 40px;
    border: 1px solid #bababc;
    border-radius: 6px;
    margin-bottom: 24px;
    margin-top: auto;

    font-family: 'Poppins';
    font-size: 14px;
    line-height: 21px;

    letter-spacing: -0.02em;

    color: #23262A;
}

.closeCross {
    width: 30px;
    height: 30px;
    position: absolute;
    right: 20px;
    top: 20px;
    cursor: pointer;
    padding: 0;
    border: none;
    background-color: transparent;
}

.plusBtn {
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    cursor: pointer;
    top: 100px;
    right: 130px;
    height: 24px;
    width: 24px;
    border-radius: 50px;
    border: none;
}
.uploadedImg {
    height: 100%;
    object-fit: cover;
}
.plusSvg {
    pointer-events: none;
    position: absolute;
    top: 100px;
    right: 130px;
}
.userInfoModal div {
    overflow: hidden;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #d9d9d9;
    border-radius: 50px;
    width: 88px;
    height: 88px;
    margin: 0 auto;
}

.addImgInput {
    position: absolute;
    border-radius: 50px;
    width: 88px;
    height: 88px;
}

.userSvg {
    width: 18px;
    height: 18px;
    position: absolute;
    bottom: 122px;
    left: 38px;
}

.userAvaSvg {
    height: 40px;
    width: 40px;
    color: #C4C4C4;
}

@media screen and (min-width: 375px) and (max-width: 767px) {
    .nameInput {
       min-width: 242px;
    }
    .userInfoModal {
        height: 330px;
        padding: 32px 24px;
    }
}

@media screen and (min-width: 768px) and (max-width: 1239px) {
    .nameInput {
       min-width: 400px;
       height: 60px;
        font-size: 18px;
        line-height: 27px;
        letter-spacing: -0.02em;
        padding-left: 50px;
    }
    .userInfoModal {
        height: 300px;
        padding: 50px 40px;
    }
    .mediaInput {
        top: 135px;
        right: 220px;
    }
    .plusBtn {
     top: 135px;
    right: 225px;
    }
    .userSvg {
        width: 24px;
        height: 24px;
        bottom: 152px;
        left: 57px;
    }
    .penSvg {
        height: 20px;
        width: 20px;
        right: 63px;
        bottom: 152px;
    }
    .userInfoModal div {
        width: 103px;
        height: 103px;
    }
    .saveBtn {
        height: 60px;
        font-size: 16px;
        line-height: 18px;
    }
    .userAvaSvg {
        height: 50px;
        width: 50px;
    }
    .plusSvg {
        top: 130px;
        right: 220px;
    }
}

@media screen and (min-width: 1240px) {
    .userInfoModal {
        padding: 60px 50px;
        height: 300px;
    }
        .nameInput {
            min-width: 400px;
            height: 60px;
            font-size: 18px;
            line-height: 27px;
            letter-spacing: -0.02em;
            padding-left: 50px;
        }
    
        .mediaInput {
            top: 135px;
            right: 220px;
        }
    
        .plusBtn {
            top: 135px;
            right: 225px;
        }
    
        .userSvg {
            width: 24px;
            height: 24px;
            bottom: 162px;
            left: 68px;
        }
    
        .penSvg {
            height: 20px;
            width: 20px;
            right: 70px;
            bottom: 163px;
        }
    
        .userInfoModal div {
            width: 103px;
            height: 103px;
        }
    
        .saveBtn {
            height: 60px;
            font-size: 16px;
            line-height: 18px;
        }
    
        .userAvaSvg {
            height: 50px;
            width: 50px;
        }

        .plusSvg {
            top: 140px;
            right: 225px;
        }
}